.gpt3__blog-container_article {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.gpt3__blog-container_article-image {
  flex: 1;
}

.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}

.gpt3__blog-container_article-description {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background: var(--color-blog);
}

.gpt3__blog-container_article-description h4 {
  color: #FFF;
  font-family: var(--font-family);
  font-size: 11.649px;
  font-weight: 700;
  line-height: 34.947px;
  margin: 1rem;
}

.gpt3__blog-container_article-description h1 {
  color: #FFF;
  font-family: var(--font-family);
  font-size: 25.11px;
  font-weight: 800;
  line-height: 30.287px;
  margin: 0 0 2rem 1rem;
}