.gpt3__blog {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.gpt3__blog-heading {
  flex: 1;
}

.gpt3__blog-heading h1 {
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -2.48px;
  margin-bottom: 3rem;
}

.gpt3__blog-container {
  flex: 3;
  display: flex;
  flex-direction: row;
}

.gpt3__blog-container-groupA {
  flex: 1;
  display: flex;
  cursor: pointer;
  margin-top: 3rem;
}

.gpt3__blog-container-groupB {
  flex: 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
  margin-left: 3rem;
  margin-top: 3rem;
  cursor: pointer;
}

@media screen and (max-width: 1100px) {
  .gpt3__blog-container {
    flex-direction: column;
  }

  .gpt3__blog-container-groupB {
    margin-left: 0;
  }
}

@media screen and (max-width: 750px) {

  .gpt3__blog-container-groupB {
    grid-template-columns: repeat(1, 1fr);
  }

  .gpt3__blog-heading h1 {
    font-size: 50px;
    font-weight: 800;
    line-height: 60px;
  }
}