.gpt3__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--color-footer);
}

.gpt3__footer-heading h1 {
  text-align: center;
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -2.48px;
  margin-top: 8rem;
}

.gpt3__footer button {
  width: 218px;
  height: 65px;
  color: #FFF;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 400;
  background: var(--color-footer);
  border: 4px solid linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  margin-top: 4rem;
  cursor: pointer;
}

.gpt3__footer-lowsection {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 10rem;
  align-items: flex-start;
  justify-content: space-between;
}

.gpt3__footer h4 {
  color: #FFF;
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 400;
}

.gpt3__footer h4:last-child {
  margin-top: 5rem;
}

.gpt3__footer-lowsection-group1 image {
  width: 118px;
  height: 30px;

}

.gpt3__footer-lowsection-group1 p,
.gpt3__footer-lowsection-group2 p,
.gpt3__footer-lowsection-group3 p,
.gpt3__footer-lowsection-group4 p {
  color: #FFF;
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 400;
  margin-top: 2rem;
}

.gpt3__footer-lowsection-group2 h4,
.gpt3__footer-lowsection-group3 h4,
.gpt3__footer-lowsection-group4 h4 {
  color: #FFF;
  font-family: var(--font-family);
  font-size: 14px;
  font-weight: 400;
  font-weight: bold;
}

@media screen and (max-width: 800px) {
  .gpt3__footer-lowsection {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .gpt3__footer-lowsection-group1,
  .gpt3__footer-lowsection-group2,
  .gpt3__footer-lowsection-group3,
  .gpt3__footer-lowsection-group4 {
    margin-top: 4rem;
  }

  .gpt3__footer-heading h1 {
    font-size: 45px;
    font-weight: 800;
    line-height: 55px;
    letter-spacing: -2.48px;
    margin-top: 8rem;
  }
}