.gpt3__features {
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-direction: row;
}

.gpt3__features-header {
  display: flex;
  flex: 1;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
  margin-right: 5rem;
}

.gpt3__features-header h1 {
  color: #FFF;
  font-family: var(--font-family);
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
}

.gpt3__features-header p {
  color: var(--color-subtext);
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  margin-top: 2rem;
}

.gpt3__features-container {
  display: flex;
  flex: 1.5;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

@media screen and (max-width: 990px) {
  .gpt3__feature {
    flex-direction: column;
  }

  .gpt3__features-header {
    margin: 0 0 2rem 0;
  }
}

@media screen and (max-width: 990px) {
  .gpt3__features {
    flex-direction: column;
  }

  .gpt3__features-header {
    margin: 0 0 2rem 0;
  }
}

@media screen and (max-width: 990px) {
  .gpt3__features-header h1 {
    font-size: 28px;
    line-height: 38px;
  }
}