.gpt3__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  background: var(--gradient-bar);
  border-radius: 1rem;
  border: none;
}

.gpt3__cta-description {
  display: flex;
  flex: 2;
  align-items: flex-start;
  flex-direction: column;
  margin: 0 1rem 0 0;
}

.gpt3__cta-description h4 {
  color: #0E0E0E;
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 500;
  line-height: 30px;
  text-align: center;
}

.gpt3__cta-description h1 {
  color: #000;
  font-family: var(--font-family);
  font-size: 24px;
  font-weight: 800;
  line-height: 45px;
}

.gpt3__cta-button {
  display: flex;
  flex: 1;
  align-items: flex-end;
  justify-content: flex-end;
  margin: 0 0 0 1rem;
}

.gpt3__cta-button button {
  width: 189.136px;
  height: 59.398px;
  border-radius: 40px;
  background: #000;
  border: none;
  color: #FFF;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  cursor: pointer;
}

@media screen and (max-width: 950px) {
  .gpt3__cta {
    flex-direction: column;
  }

  .gpt3__cta-description {
    align-items: center;
    margin: 0 0 1rem 0;
  }

  .gpt3__cta-button {
    align-items: center;
    margin: 1rem 0 0 0;
  }

  .gpt3__cta-description h1 {
    font-size: 20px;
    line-height: 38px;
    text-align: center;
  }
}